@import url(https://fonts.googleapis.com/css?family=Muli|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Zhi+Mang+Xing&display=swap);

.jumbo.cover {
    background: url('/images/background_main.jpg');
    background-size: cover;
}

a {
    color: #00B7FF;
}

.jumbo.cover {
    height: 100vh;
    display: flex;
    align-items: center;
}

#EntryField {
    left: 200px;
    height: 400px;
    position: relative;
    background: #ffffff !important;
    border-radius: 4px !important;
    padding: 32px !important;
    width: 441px !important;
    box-shadow: 0 16px 40px rgba(0, 0, 0, 0.25) !important;
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 640px) {
    #EntryField {
        left: 0px;
        right: 0px;
        width: 80% !important;
        margin: auto;
    }
}
h3.wine-title {
    text-align: center;
}

.right-booking {
    padding: 20px!important;
    background-color: #C8CDD0;
    border-radius: 5px;
    max-height: 400px;
}

.winery-description {
    display: grid!important;
    grid-template-columns: 75% 25%;
}

@media only screen and (max-width: 640px) {
    .right-booking {
        padding: 10px;
        width: auto;
        float: none;
        float: initial;
    }
    .left-info {
        padding: 80px 10px 10px 10px;
        width: auto;
        float: none;
        float: initial;
    }
    .ui.container.winery-description {
        width: auto !important;
        margin-left: 0em !important;
        margin-right: 0em !important;
    }
}
.winery-search {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.winery-search-div {
    flex-direction: column;
    display: flex;
    height: 100vh;
}

.winery-search-inner {
    display: flex;
    flex-grow: 1;
}

.winery-search-left {
    overflow-y: scroll;
    padding: 50px;
}

.winery-search-right {
    position: relative;
}

div.winery-search-right {
    width: 40%;
    float: left;
}

div.winery-search-left {
    width: 60%;
    float: left;
}

.middle-loader {
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-items: center;
    flex-direction: row;
    background-color: #fefefe;
    width: 100%;
    flex-grow: 1;
}

.inner-loader {
    margin-left: auto;
    margin-right: auto;
    padding: 24px;
    box-shadow: 0 16px 40px rgba(0, 0, 0, 0.25) !important;
    border-radius: 5px;
    background: white;
    text-align: center;
}

.desktop-hidden {
    display: none !important;
}

.mobile-hidden {
    display: inline !important;
    display: initial !important;
}

@media only screen and (max-width: 640px) {
    div.winery-search-left.mobile-map {
        width: 100%;
        float: left;
    }
    div.winery-search-right.mobile-map {
        display: none;
    }
    div.winery-search-right.mobile-list {
        width: 100%;
        float: left;
    }
    div.winery-search-left.mobile-list {
        display: none;
    }
    .desktop-hidden {
        display: inline !important;
        display: initial !important;
    }
    .mobile-hidden {
        display: none !important;
    }
}
nav.top-nav {
  border-bottom: solid 1px #3f3f3f; }

nav.top-nav ul {
  margin: 0px;
  padding: 0px;
  list-style: none;
  height: 50px;
  display: flex;
  flex-direction: row; }

nav.top-nav ul li {
  height: 100%;
  border-right: solid 1px grey;
  border-right: solid 1px grey; }

nav.top-nav ul li a {
  color: black; }

.winery-photo-viewer {
  border: solid 1px #C8CDD0;
  margin: 20px;
  margin-top: 0px;
  border-radius: 5px;
  height: 150px; }
  .winery-photo-viewer img {
    width: 200px;
    padding: 10px;
    box-sizing: content-box; }

.winery-list-item {
  padding: 10px !important;
  display: grid !important;
  grid-template-columns: 40% 60%; }

.winery-list-item:hover {
  box-shadow: 1px 1px 14px -5px black !important;
  border: solid 1px black;
  cursor: pointer; }

.content {
  padding: 25px !important; }

@media only screen and (max-width: 640px) {
  .winery-list-item {
    padding: 10px !important;
    display: grid !important;
    grid-template-columns: 40% 60%; } }

.card {
  background-color: rgba(0, 0, 0, 0.2); }

.winery-grid {
  display: grid;
  grid-template-columns: 33% 33% 33%; }
  .winery-grid .wine {
    display: flex;
    flex-direction: column; }
    .winery-grid .wine div.wine-img {
      width: 100%;
      height: 250px;
      background-size: contain !important;
      background-repeat: no-repeat !important;
      background-position: center !important; }
    .winery-grid .wine .wine-type {
      text-align: center;
      color: grey; }
    .winery-grid .wine * {
      max-width: 100%; }

.font-multi {
  font-family: "Muli", sans-serif !important; }

.font-open-sans {
  font-family: "Open Sans", sans-serif !important; }

.react-datepicker-wrapper,
.react-datepicker__input-container {
  width: 100%; }

.masthead {
  height: 500px !important;
  background-size: 100% !important;
  background-position: center !important; }

@media only screen and (max-width: 640px) {
  .masthead {
    height: 250px !important;
    background-size: cover; } }

