.winery-search {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.winery-search-div {
    flex-direction: column;
    display: flex;
    height: 100vh;
}

.winery-search-inner {
    display: flex;
    flex-grow: 1;
}

.winery-search-left {
    overflow-y: scroll;
    padding: 50px;
}

.winery-search-right {
    position: relative;
}

div.winery-search-right {
    width: 40%;
    float: left;
}

div.winery-search-left {
    width: 60%;
    float: left;
}

.middle-loader {
    display: flex;
    flex: 1;
    align-items: center;
    justify-items: center;
    flex-direction: row;
    background-color: #fefefe;
    width: 100%;
    flex-grow: 1;
}

.inner-loader {
    margin-left: auto;
    margin-right: auto;
    padding: 24px;
    box-shadow: 0 16px 40px rgba(0, 0, 0, 0.25) !important;
    border-radius: 5px;
    background: white;
    text-align: center;
}

.desktop-hidden {
    display: none !important;
}

.mobile-hidden {
    display: initial !important;
}

@media only screen and (max-width: 640px) {
    div.winery-search-left.mobile-map {
        width: 100%;
        float: left;
    }
    div.winery-search-right.mobile-map {
        display: none;
    }
    div.winery-search-right.mobile-list {
        width: 100%;
        float: left;
    }
    div.winery-search-left.mobile-list {
        display: none;
    }
    .desktop-hidden {
        display: initial !important;
    }
    .mobile-hidden {
        display: none !important;
    }
}